import React from 'react';
import { useNavigate } from 'react-router-dom'
import BlockChainContext from '../Context/blockchain/blockchainContext'
import { useContext } from 'react'



function Final() {

   const { data, setData } = useContext(BlockChainContext);
   const Navigate = useNavigate()




  
   function Click(e) {

      e.preventDefault();
      Navigate(-1);
      setData(null);
   }
  
 


  return (

    
     <div className="flex items-center justify-center h-screen">
       { data ? <div className="p-4">
        <div className="flex flex-col items-center space-y-2">
          <svg xmlns="http://www.w3.org/2000/svg" className="text-green-600 w-28 h-28" fill="none" viewBox="0 0 24 24" stroke="currentColor" stroke-width="1">
            <path stroke-linecap="round" stroke-linejoin="round" d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
          </svg>
          <h1 className="text-4xl font-bold">You voted for {data ? data.candidate : "Loading..."}</h1>
      
           
          <p className="text-lg text-center m-10 text-indigo-400">Timestamp : {data ? data.timestamp : "loading..."}</p>
          <p className="text-lg text-center m-10 text-indigo-400">Validation : {data ? data.previous_hash: "loading..."}</p>

          
          <button
            onClick={Click}
            className="inline-flex items-center px-4 py-2  text-white bg-indigo-600 border border-indigo-600 rounded rounded-full hover:bg-indigo-700">
            <span className="text-sm font-medium">
              Go Back
            </span>
          </button>
        </div>
      </div> : "Loading...."}
    </div>
  )
}

export default Final