import React from 'react';
import BlockChainContext from '../Context/blockchain/blockchainContext';
import { useEffect, useContext} from 'react';
import CandidateCard from './caniddate_card';
import { useState } from 'react';
import ConfirmModal from './confirm_modal'



export default function Elect() {

  const { getAllCandidates, persons, setId } = useContext(BlockChainContext);
  const [show, setShow] = useState(false)

  const [_id, _setId] = useState(null);
 

  useEffect(() => {

    getAllCandidates();

    // eslint-disable-next-line 
  }, [])

  function handleChange(id) {

    setShow(true);
    setId(id);
   
    

  }



   return ( 
    
    <div class=" mt-5 p-7 grid grid-cols-1 sm:grid-cols-1 md:grid-cols-3 lg:grid-cols-3 xl:grid-cols-3 gap-5">

      
      {persons.map((person) => <CandidateCard person={person} handleChange={handleChange}/>) }
      <ConfirmModal show={show} setShow={setShow} id={_id}/>


    </div>
    
     
    
    )




   
}
