import { useContext} from "react";
import React from "react";
import { toast } from "react-toastify";
import { Link, useNavigate} from "react-router-dom";
import { Navbar, Dropdown, Avatar, DarkThemeToggle} from "flowbite-react";
import BlockChainContext from '../Context/blockchain/blockchainContext';

function NavbarComponent(props) {

	const { user, setUser } = useContext(BlockChainContext);
	const nav = useNavigate();


	function SignOut(e){

         setUser(null);
		 nav('/');
		 toast.warn("logged out from voting system", {
			position: "bottom-center",
			autoClose: 5000,
			hideProgressBar: false,
			closeOnClick: true,
			pauseOnHover: true,
			draggable: true,
			progress: undefined,
			})


	}



  return (
	<div class="border border-b4 border-dark-600">
	<Navbar
	fluid={false}
	rounded={true}
  >
	<Navbar.Brand>
	  <img
		src="https://flowbite.com/docs/images/logo.svg"
		className="mr-3 h-6 sm:h-9"
		alt="Flowbite Logo"
	  />
	  <span className="self-center whitespace-nowrap text-xl font-semibold dark:text-white">
		<Link to="/">{props.title}</Link>
	  </span>
	</Navbar.Brand>

	<div className="flex md:order-2">
	
     <DarkThemeToggle />
  
	{
		
		user ?  <Dropdown
		arrowIcon={false}
		inline={true}
		label={<Avatar alt="User settings" img={`https://ui-avatars.com/api/?name=${user.name}`} rounded={true}/>}
	  >
		<Dropdown.Header>
		  <span className="block text-sm">
			{user.name}
		  </span>
		</Dropdown.Header>
		<Dropdown.Item>
		  My Polls
		</Dropdown.Item>
		<Dropdown.Item onClick={(e)=>{e.preventDefault(); nav('details')}}>
	      Voter Pass
		</Dropdown.Item>
		<Dropdown.Divider />
		<Dropdown.Item onClick={SignOut}>
		  Sign out
		</Dropdown.Item>
	  </Dropdown> : <p></p>
}
	  <Navbar.Toggle />
	</div>
	<Navbar.Collapse>
	  <Navbar.Link
		active={true}
	  >
		<Link to="/"> Home</Link>
	  </Navbar.Link>

	  <Navbar.Link href="/navbars">
		Contact
	  </Navbar.Link>
	</Navbar.Collapse>
  </Navbar>
  </div>
  );
}

export default NavbarComponent;
