import { Route, Routes } from "react-router-dom"
import React from "react"
import Home from "./home"
import Input from "./input"
import NoPage from "./nopage"
import GenerateKeys from "./generatekeys"
import Elect from "./candidates"
import Success from "./success"
import Final from "./final"
import Details from "./details"
import fake_candidates from "./fake_candidates"

export default function Urls() {





  return (
   
     
    <Routes>
      <Route index element={<Home />} />
      <Route path="generate" element={<Input placeholder="Enter CNIC here" btn_text="Check" title="Check My Voter Status" description="Enter CNIC here to Check your voting eligibility, if you are eligible, voter pass will be generated for you"/>} />
      <Route path="details" element={< Details />}/>
      <Route path="vote" element={<Elect />} />
      <Route path="*" element={<NoPage />} />
      <Route path="generatekeys" element={<GenerateKeys />} />
      <Route path="success" element={<Success candidates={fake_candidates}/>} />
      <Route path="final" element={<Final />} />
    

      </Routes>



  )
}
