import React from 'react';
import axios from 'axios';
import { useContext, useEffect, useState } from 'react'
import { toast } from 'react-toastify';
import { Button } from 'flowbite-react'
import BlockChainContext from '../Context/blockchain/blockchainContext'
import { useNavigate } from 'react-router-dom';


export default function Input(props) {

  const nav = useNavigate();
  const {setUser} = useContext(BlockChainContext);

  const [input, setInput] = useState('');
  const [button, setButton] = useState(false);

  useEffect(() => { 
      
    if(input.length < 13) 
    {setButton(true); }
    else if(input.length===13){
      setButton(false)
    }
    setUser(null);

    // eslint-disable-next-line
  }, [input])


  function handleChange(event){

     setInput(event.target.value);

  }
 
  async function handleSubmit(event)
  {

      event.preventDefault();
    
      const URL = `http://127.0.0.1:8000/api/blockchain/check/user?cnic=${input}`
  
      try {
          const response = await axios.get(URL);
          if (!response.data.message) {
              
            nav('/details');
            setUser(response.data);
           
            
            
          } else {
  
            toast.error(response.data.message, {
              position: "top-center",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
              })
  
              setUser(null);
            
          }
        
      }
      catch (error) {
        toast.error("No response from server", {
          position: "top-center",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          })
      }
  
    
     
        
  }


  return (

    <>
     
    <div className="flex items-center justify-center h-screen">
      <div className="rounded-2xl border shadow-x1 p-8 max-w-lg">
        <div className="flex flex-col items-center space-y-4">
          <h1 className="font-bold text-2xl text-indigo-700 w-4/6 text-center">
            {props.title}
          </h1>
          <p className="text-sm text-indigo-500 text-center w-5/6">
            Enter your National ID card Number 
            
          </p>
          <input
            type="text"
            input={input}
            onChange={handleChange}
            placeholder={props.placeholder}
            className={`border-2 text-indigo-500 rounded-lg w-full h-12 px-4`}
          />
          <p className="text-xs text-indigo-500 text-center w-5/6">{ input.length < 13 ? `${13-input.length+1} characters required` : <></> }</p>
          <Button
           disabled={button} onClick={handleSubmit}
          >
        
            {props.btn_text}
          </Button>
          <p className="text-sm text-indigo-500 text-center w-5/6">
          </p>
        </div>
      </div>
    </div>
    </>
    
  )
}
