import { useState } from "react";
import React from "react";
import BlockChainContext from "./blockchainContext";

import axios from "axios";
import { toast } from "react-toastify";



function BlockChainState(props) {


   const [user, setUser] = useState('');
   const [url, setUrl] = useState('');
   const [votes, setVotes] = useState('');
   const [keys, setKeys] = useState('');
   const [persons,setPersons] = useState([]);
   const [data, setData] = useState([]);
   const [id, setId] = useState('');
   const [candidate, setCandidate] = useState('');
   const [name, setName] = useState('');
  

  async function getUserCnic() {


    const URL = `http://127.0.0.1:8000/api/blockchain/check/user?cnic=${url}`

    try {
        const response = await axios.get(URL);
        if (!response.data.message) {

          setUser(response.data);
          
        } else {

          toast.error(response.data.message, {
            position: "top-center",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            })

            setUser(null);
          
        }
      
    }
    catch (error) {
      toast.error("No response from server", {
        position: "top-center",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        })
    }

  }

  async function getKeys() {

    const URL = "http://127.0.0.1:8000/api/blockchain/generate_keys"


    try {

      const response = await axios.get(URL);
      setKeys(response.data);
      localStorage.setItem("public_key",response.data.public_key)
    }
    catch(error) {

      alert(error.message);
    }

  }

  async function getAllCandidates() {

    const URL = "http://127.0.0.1:8000/api/system/all_candidates"

    try {

      const response = await axios.get(URL);
      setPersons(response.data);
     

    
    }
    catch(error) {

      toast.error("No response from server", {
        position: "top-center",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        })

    }

    


  }


  async function get_candidate(){

    const url = `http://localhost:8000/api/blockchain/get_candidates/${id}`

    await axios.post(url)
    .then(function (response) {
      setCandidate(response.data);
    })
    .catch(function (error) {
      toast.error("No response from server", {
        position: "top-center",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        })
      
    });
  }


async function Polling(){
    
  const URL = `http://localhost:8000/api/blockchain/poll?id=${id}`


  await axios.post(URL)
  .then(function (response) {
    setData(response.data);
    
  })
  .catch(function (error) {
    toast.error(error.message, {
      position: "top-center",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      })
    
  });

}


async function get_votes(){
    
  const url = `http://localhost:8000/api/blockchain/check_votes/${name}`;

  await axios.post(url)
  .then(function (response) {
    setVotes(response.data);
    
  })
  .catch(function (error) {
    alert(error);
    
  });





}


    
    return (
            <div>
              <BlockChainContext.Provider value={{ getUserCnic , user, setUser, setUrl, url, keys, getKeys, persons, getAllCandidates, Polling, data, setId, get_candidate, candidate, get_votes, setName, votes}}>
                {props.children}
              </BlockChainContext.Provider>
            </div>
          )



    
}


export default BlockChainState
