import React, { useContext, useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import BlockChainContext from '../Context/blockchain/blockchainContext'
import Spinner from './spinner';

export default function GenerateKeys(props) {

   const Nav = useNavigate()
   const { keys, getKeys } = useContext(BlockChainContext);
   const [visibility, setVisibility] = useState(false);
   const [pvisibility, setpVisibility] = useState(false);

   useEffect(() => {


    getKeys();
   // eslint-disable-next-line 
  },[])


  function ShowPublicKey(event) {

    event.preventDefault();
    setVisibility(!visibility)


  }
  
  function ShowPrivateKey(event) {

    event.preventDefault();
    setpVisibility(!pvisibility)


  }


  function Condition(keys) {


     if (!keys) {

       return <Spinner />

     }

     if(keys) {

      return (<div className="container px-5 py-24 m-auto flex flex-wrap">
    <div className="flex flex-wrap -m-4">
      <div className="p-4 lg:w-full md:w-full">
        <div className="flex border-2 rounded-lg border-gray-200 border-opacity-50 p-8 sm:flex-row flex-col">
          <div className="w-16 h-16 sm:mr-8 sm:mb-0 mb-4 inline-flex items-center justify-center rounded-full bg-yellow-100 text-yellow-500 flex-shrink-0">
          </div>
          <div className="flex-grow">
            <h2 className="text-green-500 text-md title-font font-medium mb-3">Public Key</h2>
            <p className="leading-relaxed text-base text-red-500">{keys.public_key}</p>
            <p className="mt-3 text-green-500 inline-flex items-center" onClick={ShowPublicKey}>
            </p>
          </div>
        </div>
      </div>
      <div className="p-4 lg:w-full md:w-full">
        <div className="flex border-2 rounded-lg border-gray-200 border-opacity-50 p-8 sm:flex-row flex-col">
          <div className="w-16 h-16 sm:mr-8 sm:mb-0 mb-4 inline-flex items-center justify-center rounded-full bg-yellow-100 text-yellow-500 flex-shrink-0">
         
          </div>
          <div className="flex-grow">
            <h2 className="text-green-500 text-md title-font font-medium mb-3">Private Key</h2>
            <p className="leading-relaxed text-base text-red-500">{pvisibility ? keys.private_key : "Hidden"}</p>
            <p className="mt-3 text-green-500 inline-flex items-center"onClick={ShowPrivateKey}>Toggle Visibility of Private key
              <svg fill="none" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" className="w-4 h-4 ml-2" viewBox="0 0 24 24">
                <path d="M5 12h14M12 5l7 7-7 7"></path>
              </svg>
            </p>
          </div>
        </div>
      </div>
     { keys ? <button onClick={(event)=> { event.preventDefault(); Nav('/Vote') }} className="flex mx-auto  text-white bg-green-500 border-0 py-2 px-8 focus:outline-none hover:bg-green-600 rounded text-lg">Vote</button> : <></> }
    </div>
  </div> )

     }

  }


  return (

    Condition(keys)
    

  )
}
