
import { useNavigate} from 'react-router-dom'
import { useContext } from 'react';
import BlockChainContext from '../Context/blockchain/blockchainContext';

//import { useState } from "react"
import Spinner from './spinner';



export default function Details() {

  const { user, getKeys, keys } = useContext(BlockChainContext);
  const nav = useNavigate();



  function Vote(e){
     
    e.preventDefault();
    nav('/vote')

  }


  function KeysCollect(e){

     e.preventDefault();
     getKeys();

  }

   return user ? (

    <>
      
      <h1 class="text-center m-10 text-2xl text-indigo-600">Voter Details</h1>

   
  
          <div class="mx-10 w-full md:w-9/12 mx-2 h-64">
  
            <div class="bg-white p-3 border border-dark-300 shadow-sm rounded-sm text-indigo-600">
              
              <div class="text-green-600">
                <div class="grid md:grid-cols-2 text-sm">
                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">Name</div>
                    <div class="px-4 py-2">{user.name}</div>
                  </div>
                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">Polled a Vote</div>
                    <div class="px-4 py-2">{user.has_voted ? "Already Polled a Vote" : "Ready to Vote"}</div>
                  </div>
                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">Current Address</div>
                    <div class="px-4 py-2">{user.address}</div>
                  </div>
                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">Permanant Address</div>
                    <div class="px-4 py-2">{user.address}</div>
                  </div>
                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">CNIC</div>
                    <div class="px-4 py-2">
                      <p class="text-blue-800">{user.cnic}</p>
                    </div>
                  </div>
                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">Private Key</div>
                    <div class="px-4 py-2">{user.private_key ? `${user.private_key.slice(1,12)}...` : "loading...." }</div>
                  </div>
                </div>
              </div>
              <div class="lg:flex px-2">
              <button onClick={Vote}
                class="block w-1/2 text-white-800 text-sm font-semibold rounded-lg hover:bg-gray-100 focus:outline-none focus:shadow-outline focus:bg-gray-100 hover:shadow-xs my-4 p-2 mx-2">Vote</button>
                <button onClick={KeysCollect}
                class="block w-1/2 text-white-800 text-sm font-semibold rounded-lg hover:bg-gray-100 focus:outline-none focus:shadow-outline focus:bg-gray-100 hover:shadow-xs my-4 p-2">Generate Keys</button>
                </div>
            </div>
            <div class="bg-white p-3 mt-2 border border-dark-300 shadow-sm rounded-sm">
  
              <div class="grid grid-cols-2 ">
                <div>
                  <div class="flex items-center space-x-2 font-semibold text-gray-900 leading-8 mb-3">
                    <span clas="text-green-500">
                      <svg class="h-5" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24"
                        stroke="currentColor">
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2"
                          d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                      </svg>
                    </span>
                    <span class="tracking-wide">Previous Votes</span>
                  </div>
                  <ul class="list-inside space-y-2">
                    <li>
                      <div class="text-teal-600">Leauge of Shadows</div>
                      <div class="text-gray-500 text-xs">Bruce Wayne</div>
                    </li>
                  </ul>
                </div>
                <div>
                  <div class="flex items-center space-x-2 font-semibold text-gray-900 leading-8 mb-3">
                    <span clas="text-green-500">
                      <svg class="h-5" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24"
                        stroke="currentColor">
                        <path fill="#fff" d="M12 14l9-5-9-5-9 5 9 5z" />
                        <path fill="#fff"
                          d="M12 14l6.16-3.422a12.083 12.083 0 01.665 6.479A11.952 11.952 0 0012 20.055a11.952 11.952 0 00-6.824-2.998 12.078 12.078 0 01.665-6.479L12 14z" />
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2"
                          d="M12 14l9-5-9-5-9 5 9 5zm0 0l6.16-3.422a12.083 12.083 0 01.665 6.479A11.952 11.952 0 0012 20.055a11.952 11.952 0 00-6.824-2.998 12.078 12.078 0 01.665-6.479L12 14zm-4 6v-7.5l4-2.222" />
                      </svg>
                    </span>
                    <span class="tracking-wide">Crdentials</span>
                  </div>
                  <ul class="list-inside space-y-2">
                    <li>
                      <div class="text-teal-600">Private Key</div>
                      <div class="text-gray-500 text-xs">{user.private_key ? user.private_key : keys.private_key}</div>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>

      </>
    
   ) : <Spinner />

     

  
}
