import React from "react";
import { useContext, useState } from "react"
import Loader from "./loader";
import BlockChainContext from "../Context/blockchain/blockchainContext"
import {Modal,Button} from 'flowbite-react'
import ModalContent from "./modal_content";
import { useNavigate } from "react-router-dom";

function ConfirmModal(props) {

    const { Polling } = useContext(BlockChainContext);
    const [Loading, setLoading ] = useState();
    const Navigate = useNavigate();


    

    
    function handleClick(event){
    
        event.preventDefault();
        setLoading(!Loading);
        Polling();
        Navigate('/final');
    
    
    }

  


  return (
    <Modal
    show={props.show}
    size="xl"
    onClose={(e)=>{e.preventDefault(); props.setShow(!props.show)}}
  >
    <Modal.Header>
      Polling Confirmation
    </Modal.Header>
    <Modal.Body>
      <div className="space-y-6 p-6">
      <ModalContent name={"Name"} public_key={"9b7eghsbjyjg1163hklajssjk"} votes={"Votes"}/>
      </div>
    </Modal.Body>
    <Modal.Footer>
      <Button onClick={handleClick}>
      {Loading ? <Loader/> : "Confirm"}
      </Button>
      <Button
        color="gray"
        onClick={(e)=>{e.preventDefault(); props.setShow(!props.show)}}
      >
        Cancel
      </Button>
    </Modal.Footer>
  </Modal>
  )
}

export default ConfirmModal