import React from "react";
import axios from "axios"
import { useContext, useEffect, useState } from "react"
import Loader from "./loader";
import BlockChainContext from "../Context/blockchain/blockchainContext"
import { useNavigate } from "react-router-dom";

export default function Success(props) {
 
const [data, setData] = useState([]); 
const { vote } = useContext(BlockChainContext);
const [Loading, setLoading ] = useState()
let Navigate = useNavigate();

async function Polling(){

    const URL = 'http://localhost:8000/api/blockchain/poll'
    
    let parsed = {
        public_key : localStorage.getItem("public_key"),
        candidate : {
            name : vote.name,
            party : vote.party,
            seat : vote.slogan
        }
    }

    await axios.post(URL,parsed)
    .then(function (response) {
      console.log(response.data.message)
      Navigate('/final');
    })
    .catch(function (error) {
      alert(error);
      
    });


  }

async function getVotes(parameter) {

    const URL = `http://127.0.0.1:8000/api/blockchain/check_votes/${name}`

    try {

           const response = await axios.get(URL);
           setData(response.data)

    }
    catch(error){

        console.error(error)
    }
}

const  name = vote.name

useEffect(() => {

  
 getVotes(name);

// eslint-disable-next-line
}, [data])

function handleClick(event){

    event.preventDefault();
    setLoading(!Loading);
    Polling();


}




    return (
   
      <div className="container px-5 py-24 mx-auto">
        <div className="text-center mb-20">
            <h1 className="sm:text-3xl text-2xl font-medium text-center title-font text-gray-900 mb-4">Procced with voting for {vote.name} ?</h1>
            <p className="text-base leading-relaxed xl:w-2/4 lg:w-3/4 mx-auto">This person has {data ? data.votes : "Loading...."} Votes so far</p>
        </div>
        <div className="flex flex-wrap lg:w-4/5 sm:mx-auto sm:mb-2 -mx-2">
            <div className="p-2 sm:w-1/2 w-full">
                <div className="bg-gray-100 rounded flex p-4 h-full items-center">
                    <i className='bx bxs-user px-3 text-green-500 bx-sm'></i>
                    <span className="title-font font-medium">{vote.name}</span>
                </div>
            </div>
            <div className="p-2 sm:w-1/2 w-full">
                <div className="bg-gray-100 rounded flex p-4 h-full items-center">
                    <i className="bx bxs-id-card px-3 text-green-500 bx-sm"></i>
                    <span className="title-font font-medium">{vote.party}</span>
                </div>
            </div>
        </div>
        <button onClick={handleClick} className="flex mx-auto mt-16 text-white bg-green-500 border-0 py-2 px-8 focus:outline-none hover:bg-green-600 rounded text-lg">{Loading ? <Loader/> : "Confirm"}</button>
    </div>
  )
}
